
:root{
    --BackgroundCovered: #282c34;
    --BackgroundUncovered: white;

    --MessageColorCovered: white;
    --MessageColorUncovered: black;
    --MessageColorFooter: #fa7233;

    --HighlightColor: yellow;
    --KeyMessageColor: #ff2233;
    --TopbarColor: #61dafb;
}

.global-link{
    color: #61dafb;
}

.global-btn{
    font-size: 20px;
    background-color: #61dafb;
}

.myfooter{
    position: absolute;
    width: 100%;
    height: 7%;
    font-size: calc(6px);
    color: var(--MessageColorFooter);
}

